<template>
  <div
    ref="scrollContainer"
    class="relative bottom-0 snap-y snap-mandatory overflow-y-scroll overflow-x-hidden"
    :style="{ height: `${windowHeight}px` }"
  >
    <NavBar />
    <!-- Hero Section -->
    <div
      class="relative bottom-0 snap-start flex flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <HeroSection id="hero" />
      <PageScroll />
    </div>

    <!-- ToolSection과 MeSection (태블릿 이상) -->
    <div
      class="hidden relative bottom-0 snap-start tablet:flex flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <ToolSection id="toolsMe" />
      <MeSection />
    </div>

    <!-- ToolSection (모바일) -->
    <div
      class="flex relative bottom-0 tablet:hidden flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <ToolSection id="tools" />
    </div>

    <!-- MeSection (모바일) -->
    <div
      class="flex relative bottom-0 tablet:hidden flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <MeSection id="me" />
    </div>

    <!-- Portfolio Section -->
    <div
      class="relative bottom-0 snap-start flex flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <PortfolioSection id="portfolio" />
    </div>

    <!-- Footer -->
    <div
      class="relative bottom-0 snap-start flex flex-col items-center justify-center"
      :style="{ height: `${windowHeight}px` }"
    >
      <SiteFooter id="SiteFooter" />
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/sections/NavBar.vue";
import HeroSection from "@/components/sections/HeroSection.vue";
import PageScroll from "@/components/partials/PageScroll.vue";
import ToolSection from "@/components/sections/ToolSection.vue";
import MeSection from "@/components/sections/MeSection.vue";
import PortfolioSection from "@/components/sections/PortfolioSection.vue";
import SiteFooter from "@/components/sections/SiteFooter.vue";

export default {
  name: "HomePage",
  components: {
    NavBar,
    HeroSection,
    PageScroll,
    ToolSection,
    MeSection,
    PortfolioSection,
    SiteFooter,
  },

  data() {
    return {
      windowHeight: window.innerHeight, // 현재 창 높이
    };
  },

  methods: {
    updateWindowHeight() {
      this.windowHeight = window.innerHeight; // 창 높이 업데이트
    },

    enableSmoothScroll() {
      const container = this.$refs.scrollContainer;

      let isScrolling;
      container.addEventListener("wheel", (event) => {
        const isModalOpen = document.body.classList.contains("no-scroll");
        if (isModalOpen) return;

        event.preventDefault();

        clearTimeout(isScrolling);

        const delta = event.deltaY > 0 ? 1 : -1;
        const scrollAmount = container.scrollTop + delta * this.windowHeight;

        container.scrollTo({
          top: scrollAmount,
          behavior: "smooth",
        });

        // Prevent rapid scrolling
        isScrolling = setTimeout(() => {
          isScrolling = null;
        }, 500);
      });
    },
  },

  mounted() {
    this.enableSmoothScroll();
    window.addEventListener("resize", this.updateWindowHeight); // 창 크기 변경 시 높이 업데이트
  },

  beforeUnmount() {
    window.removeEventListener("resize", this.updateWindowHeight); // 이벤트 리스너 제거
  },
};
</script>

<style scoped></style>
